body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.form{
display: grid;
grid-template-columns: 1fr ;
grid-gap: 10px;
}

input {
    padding: .7rem;
    outline: none;
    border: none;
    background: none;
    border-bottom: 3px solid rgb(202, 191, 191, 0.5);
    cursor: pointer;
    text-align: center;
    font-size: 1rem;
}

button{
    cursor: pointer;
    padding: .5rem;
    text-transform: uppercase;
    border: none;
    background-color: rgb(76, 111, 177);
    color: white;

    outline: none;

}

.exit{
    background-color: white;


    
    
}

.done-btn {
    background-color: white;

}

.fa-check-circle{
    color: rgb(84, 163, 84);
}

.fa-times-circle {
    color: rgb(219, 80, 80);
}


.done p {
    background-color:rgb(29, 143, 29, 0.2) ;
    padding: .5rem;
}


.end {
    color: white;
    background-color: rgb(218, 28, 28);
    padding: .8rem;
}

.done-task {
    display: flex;
    justify-content: space-between;

}

.done_tasks {
    display: flex;
    flex-direction: column;
}


.App{
  display: grid;
  align-self: center;
  min-height: 100vh;



}

.box{
  background-color: rgb(251, 255, 251);
  padding: 1rem;
}

h1{
  text-align: center;
  color: rgb(80, 72, 72);
}

@media  (min-width: 800px) {
.App{
  grid-template-columns: minmax(200px, 400px );
  justify-content: center;
}
}
